<template>
    <div id="login">

        <div class="dome">
            <div class="node1">
                <h3>登录</h3>
            </div>

            <div class="node2">
                <span class="iconfont butt1">&#xe61e;</span>
                <input type="text" v-model="myuser.user">
                <span class="iconfont butt2">&#xe703;</span>
                <input type="password" v-model="myuser.password">
            </div>

            <div class="node3" >
                <a class="butt3" href="/sign"><span>用户注册</span></a>
                <button class="butt4" @click="login">登录</button>
            </div>

        </div>


    </div>

</template>

<script>
    import axios from 'axios'
    export default {
        name: "index",
        data(){
            return{
                myuser:{
                    user:'',
                    password:'',
                }
            }
        },
        methods:{
            login(){
                var that=this;
                axios.post('http://localhost:3000/login',that.myuser)
               .then(function (res) {
                   console.log(res.data);

                   if (res.data.code==0){
                       window.location="/home";
                       return alert(res.data.msg)
                   }else {

                       return alert(res.data.msg)
                   }


               }).catch(function (err) {
                        console.log(err)


                })
            }

        }
    }
</script>

